





























































import { Component, Prop, Vue, Watch, Emit } from 'vue-property-decorator';
import { accountStore } from "@/stores/modules/account"
import { memberStore } from "@/stores/modules/member";
import { dialogStore } from "@/stores/modules/dialog"
import { gradeStore } from "@/stores/modules/grade"
import { menuStore } from "@/stores/modules/menu"
import { contentStore } from "@/stores/modules/content";


@Component
export default class Members extends Vue {
  //stores
  get currentUser(){ return accountStore.currentUser }
  get members(){ return memberStore.members }
  get grades(){ return gradeStore.grades }
  get gradeInfoVO(){ return gradeStore.gradeInfoVO }
  get snackBar(){ return dialogStore.snackBar }
  get ageVO(){return memberStore.ageVO}
  get genderCountVO(){return memberStore.genderCountVO}
  get contents(){return contentStore.contents}

  //local data
  get year(){
    return parseInt(this.today.toString().slice(0,4))+1;
  }
  today:number = parseInt(this.$moment(new Date()).format('YYYYMMDD'));
  search:string = '';

  sortBy:string = '';
  descending:boolean = false;
  
  options:any[] = [{"text":"$vuetify.dataIterator.rowsPerPageAll","value":-1}, 10,20,];
  headers:any[] = [
    {
      text: '이름',
      align: 'left',
      value: 'name'
    },
    {
      text : '나이',
      align: 'left',
      value: 'age'
    },
    {
      text : '가입일',
      align: 'left',
      value: 'joinDate'
    },
    {
      text : '남은일수',
      align: 'center',
      value: 'dMinus'
    },
    {
      text : '마지막 참석일',
      align: 'center',
      value: 'lastDate'
    },
    {
      text : '마지막 참여일로부터',
      align: 'center',
      value: 'dPlus'
    },
    {
      text : '성별',
      align: 'left',
      value: 'gender',
    },
    {
      text : '회원등급',
      align: 'left',
      value: 'grade',
    },
    {
      text : '선호활동',
      align: 'center',
      value: 'hobbys',
    },
  ];

  async created(){
    menuStore.setProgress(true);
    Promise.all([
      contentStore.getContents(), 
      memberStore.getMembersInActive(),
    ]).then(async (done)=>{
      console.log('done : ', done);
      memberStore.setMembersInfoByKeys(this.members.map(member=>member.key||''));
      menuStore.setProgress(false);
    })
  }

  goMeberDetail(key:string){
    this.$router.push(`/members/detail/${key}`);
  }

  getTextByStatus(item:any){
    let text = '';
    if(item.status === 'red'){
      text = '강퇴대상'
    } else if(item.status === 'blue' && item.grade == 1){
      text = '운영진'
    } else if(item.status === 'blue' && item.grade == 5){
      text = '특수회원'
    } else if(item.status === 'green'){
      text = '모임장'
    } else {
      text = item.dMinus + '일'
    }
    return text;
  }

  getHobby(item:IMemberTypes){
    let hobbyText = '';
    const hobby = item.hobbys && this.contents.find((content:any)=> content.key == [item.hobbys[0]])

    if(hobby){
      hobbyText = hobby.name
    }
    if(item.hobbys.length > 1){
      hobbyText+=` 외 ${item.hobbys.length-1}개`
    }
    return hobbyText
  }
  

}
